}
.todo-add {
  padding: 0 15px;
}
.todo-add__form {
  position: relative;
  display: flex;
}
.todo-add__input {
  outline: none;
  border: none;
  padding: 5px;
  width: 100%;
  font-size: 14px;
}
.todo-add__input:focus + .todo-add__input-line::after {
  left: 0;
  right: 25px;
  width: 100%;
}
.todo-add__input-line {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 25px;
  height: 2px;
  background-color: #009688;
}
.todo-add__input-line::after {
  content: '';
  position: absolute;
  left: 50%;
  right: 50%;
  width: 0;
  height: 2px;
  background-color: #004d40;
  transition: all 300ms;
}
.todo-add__button {
  background: none;
  border: none;
  outline: none;
  font-size: 25px;
  color: #009688;
  cursor: pointer;
  transition: color 300ms;
}
.todo-add__button:hover,
.todo-add__button:focus {
  color: #004d40;
}
.footer {
  padding: 15px;
}
.filter {
  list-style: none;
  padding: 0;
}
.filter__item {
  display: inline-block;
  padding: 2px;
  text-decoration: none;
  color: #009688;
}
.filter__item--active {
  color: #004d40;
}
